import React from 'react';
import {ButtonLinkMain} from "../../../components/button/button";
import styled from "styled-components";
import {FlexWapper} from "../../../components/FlexWapper";
import {Container} from "../../../components/Container";
import {Theme} from "../../../styles/Theme";
import {MainIcon} from "./mainIcon/mainIcon";
import Typewriter from 'typewriter-effect';
import Tilt from 'react-parallax-tilt';

export const Main = () => {
    return (
        <MainSection>
            <Container>
                <BlockContent>
                    <BlockName>
                        <FlexWapper direction={"column"}>
                            <StyleSpan>Hi👋, I’m a</StyleSpan>
                            <StyleName>
                                <p>Software Developer</p>
                                <Typewriter
                                    options={{
                                        strings: ['Software Developer'],
                                        autoStart: true,
                                        loop: true,
                                    }}
                                />
                            </StyleName>
                            <StyleP>I’m <span>John Doe</span>, a developer dedicated to making the world a better place
                                one
                                line of
                                code at a time.</StyleP>
                            <ButtonLinkMain href="/#">Hire me</ButtonLinkMain>
                        </FlexWapper>
                    </BlockName>
                    <Tilt>
                    <MainIcon/>
                    </Tilt>
                </BlockContent>
            </Container>
        </MainSection>
    );
};

const MainSection = styled.section`

`

const BlockName = styled.form`
    width: 550px;
    margin: 120px 0px 0px 0px;
    @media ${Theme.media.tablet} {
        margin: 30px;
    }
`

const BlockContent = styled.div`
    position: relative;
    display: grid;
    grid-template-columns: 3fr 1fr;

    @media screen and (max-width: 768px) {
        display: grid;
        grid-template-columns: auto;
        grid-template-rows: 1fr 1fr;
        align-content: center;
        justify-items: center;
        justify-content: center;

        ${BlockName} {
            width: 100%;
            grid-area: 2/1/3/2;
        }
    }
`

const StyleSpan = styled.span`
    font-size: 28px;
    font-weight: 600;
    color: ${Theme.colors.colorTextName};
`

const StyleName = styled.h1`
    margin: 0;
    font-size: 32px;
    font-weight: 600;
    color: ${Theme.colors.colorTextName};
    
    p{
        display: none;
    }
`

const StyleP = styled.p`
    font-size: 18px;
    font-weight: 400;
    color: rgba(0, 0, 0, 0.7);

    span {
        font-weight: bold;
    }
`


