import styled from "styled-components";
import React from 'react';
import {Theme} from "../../../../styles/Theme";
import IconZigzag from "../../../../assets/image/zigzags.svg";
import IconPlus from "../../../../assets/image/plus.svg";
import IconCube from "../../../../assets/image/cube.svg";
import IconEllips from "../../../../assets/image/ellipse.svg";
import IconCircles from "../../../../assets/image/circlesOne.svg";
import Avatar from "../../../../assets/image/avatar.webp";

export const MainIcon = () => {
    return (
        <BlockImage>
            <ImageZigzag src={IconZigzag}/>
            <ImagePlus src={IconPlus}/>
            <ImageCube src={IconCube}/>
            <ImageEllips src={IconEllips}/>
            <ImageCircles src={IconCircles}/>
            <StyleImg src={Avatar}/>
        </BlockImage>
    );
};




const ImageZigzag = styled.img `
`
const ImagePlus = styled.img `
`
const ImageCube = styled.img `
`
const ImageEllips = styled.img `
`
const ImageCircles = styled.img `
`

const BlockImage = styled.div`
    position: relative;

    ${ImageZigzag} {
        position: absolute;
        transform: translateX(-40px) translateY(30px);
    }

    ${ImagePlus} {
        position: absolute;
        transform: translateX(125px);
    }

    ${ImageCube} {
        position: absolute;
        transform: translateX(325px);
    }

    ${ImageEllips} {
        position: absolute;
        transform: translateY(450px) translateX(-20px);
    }

    ${ImageCircles} {
        position: absolute;
        transform: translateX(245px) translateY(420px);
    }

    @media ${Theme.media.mobile} {
        ${ImageZigzag} {
            position: absolute;
            transform: translateX(-50px) translateY(30px);
            width: 35px;
        }

        ${ImagePlus} {
            position: absolute;
            transform: translateX(70px) translateY(10px) rotate(-15deg);
            width: 20px;
        }

        ${ImageCube} {
            position: absolute;
            transform: translateX(190px) translateY(20px);
            width: 30px;
        }

        ${ImageEllips} {
            position: absolute;
            transform: translateY(295px) translateX(-25px);
            width: 20px;
        }

        ${ImageCircles} {
            position: absolute;
            transform: translateX(110px) translateY(270px);
        }
    }
`

const StyleImg = styled.img`
    width: 345px;
    height: 390px;
    margin: 54px 0px 40px 0px;

    @media ${Theme.media.mobile} {
        width: 204px;
        height: 238px;
    }
`