import React from 'react';
import styled from "styled-components";
import {FooterNav} from "./footerNav/footerNav";
import {FlexWapper} from "../../components/FlexWapper";
import FoneBcg from "../../assets/image/bcgFone.svg"
import {Theme} from "../../styles/Theme";
import {font} from "../../styles/Common";

export const Footer = () => {
    return (
        <FooterBlock>
            <img src={FoneBcg} alt={"asrg"}/>
            <ContainerFooter>
                <BlockSocial>
                    {/*  left part  */}
                    <TitleSocial>My social media links:</TitleSocial>
                    <BlockIcon>
                        <FooterNav IconId={"in"}/>
                        <FooterNav IconId={"whatsApp"}/>
                        <FooterNav IconId={"twitter"}/>
                        <FooterNav IconId={"gmail"}/>
                    </BlockIcon>

                    {/*  right part  */}
                    <TitleMoreSocial>More projects I’ve worked on</TitleMoreSocial>
                    <BlockMoreSocial>
                        <FlexWapper gap={"22px"}>
                        <FooterNav IconId={"gitHub"}/>
                        <EmailP>@<span>john-doe</span>on github</EmailP>
                        </FlexWapper>
                        </BlockMoreSocial>
                </BlockSocial>
            </ContainerFooter>
        </FooterBlock>
    );
};

const FooterBlock = styled.footer`
    position: relative;
    
    img {
        position: absolute;
        z-index: -1;
        width: 100%;
        background-color: #F7F6FB;
        height: 444px;

        @media screen and (max-width: 768px) {
            height: 280px;
        }
        @media screen and (max-width: 560px) {
            height: 260px;
        }
    }

`

const TitleSocial = styled.p`
    font-size: 20px;
    font-weight: 500;
    color: ${Theme.colors.colorTextButton};
`


const BlockIcon = styled.div`
    display: flex;
    gap: 65px;
`

const TitleMoreSocial = styled.p`
    font-size: 20px;
    font-weight: 500;
    color: ${Theme.colors.colorTextButton};
`

const BlockMoreSocial = styled.div`

`

const EmailP = styled.div`
    font-size: 18px;
    font-weight: 400;
    color: ${Theme.colors.colorTextButton};

    span {
        background-image: repeating-linear-gradient(#20ECD3, #20d4ec);
        color: transparent;
        -webkit-background-clip: text;
    }
`

const ContainerFooter = styled.div`
    padding: 0px 15px;
    margin: 0 auto;
    border: 1px solid red;
    max-width: 1250px;
    transform: translateY(150px);

    @media screen and (max-width: 980px) {
        transform: translateY(150px);
    }
    @media screen and (max-width: 768px) {
        transform: translateY(40px);
        ${TitleSocial}, ${TitleMoreSocial}, ${BlockIcon} {
            font-size: 18px;
            gap: 57px;
        }
    }
    @media screen and (max-width: 562px) {
        transform: translateY(20px);
    }
}
`

const BlockSocial = styled.form`

    display: grid;
    grid-template-columns: 2fr 1fr;
    grid-template-rows: 1fr 1fr;
    align-content: center;

    ${BlockIcon} {
        grid-area: 2/1/3/2;
    }

    @media screen and (max-width: 768px) {
        display: grid;
        justify-content: center;
        grid-template-columns: 350px;
        grid-template-rows: 1fr 1fr 1fr 1fr;
        ${EmailP} {
            grid-area: 4/1/5/2;
        }
    }
`