import React from 'react';
import {SkillsTitle} from "../../../components/titles/sectionTitles";
import {Description, Skill} from "./skill/skill";
import styled from "styled-components";
import {FlexWapper} from "../../../components/FlexWapper";
import PhotoItem from "../../../assets/image/SkillsBcgPhoto.webp"
import {Container} from "../../../components/Container";
import {Theme} from "../../../styles/Theme";

export const Skills = () => {
    return (
        <SkillsSection id={"skills"}>
            <Container>
            <SkillsTitle>Skills</SkillsTitle>
                <SkillsBlock>
                <Block>
                    <Description description={"I have a vast experience in the following web technologies:"}/>
                    <BlockTechStack>
                        <Skill IconId={"html"}/>
                        <Skill IconId={"css"}/>
                        <Skill IconId={"JavaScript"}/>
                        <Skill IconId={"bootstrap"}/>
                    </BlockTechStack>
                </Block>
                <Image src={PhotoItem}/>
                </SkillsBlock>
            </Container>
        </SkillsSection>
    );
};

const SkillsSection = styled.section`

`

const Block = styled.div`
    
`

const BlockTechStack = styled.div`
    display: flex;
    flex-wrap: wrap;
    gap: 42px;

    @media screen and (max-width: 768px) {
        display: grid;
        grid-template-columns: 100px 100px;
        gap: 0;
        row-gap: 10px;
        justify-content: center;
        align-items: center;
        justify-items: center;
        margin: 0px 0px 20px 0px;

`

const Image = styled.img`
    width: 511px;
    height: 414px;
    @media ${Theme.media.mobile} {
        width: 250px;
        height: 186px;
    }
`

const SkillsBlock = styled.form`
    display: grid;
    grid-template-columns: 3fr 1fr;
    
    @media screen and (max-width: 768px) {
        display: grid;
        grid-template-columns: 1fr;
        grid-template-rows: 1fr 1fr;
        justify-content: center;
        align-items: center;
        justify-items: center;

        ${Image} {
            grid-area: 1/1/2/3;
        }

        ${Block} {
            grid-area: 2/1/3/3;
        }
    }
}

`